.footer{
  padding: 3.75em 0 1.875em;
  background-color: #151B30;
  color: #fff;
  &__logo{
    flex-shrink: 0;
    display: block;
    width: 13.0625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    margin-top: .75em;
  }
  .socials{
    margin-top: 1.875em;
    &__item{
      flex-shrink: 0;
      background-color: rgba(#fff,.04);
      &:not(:hover){
        opacity: .5;
      }
      &:hover{
        border-color: #fff;
      }
    }
    &__hint{
      color: $p;
      opacity: .5;
      max-width: 13.5em;
      ._view2{
        display: none;
      }
    }
  }
}

.footer-top{
  display: flex;
  justify-content: flex-end;
  &__col1{
    margin-right: auto;
  }
}

.footer-nav{
  margin-left: 6.75em;
  &__btn{
    display: none;
  }
  &__hidden{
    margin-top: .625em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 144%;
    opacity: .5;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 .625em);
    display: flex;
    align-items: flex-start;
    img{
      margin-right: .5em;
      width: 1em;
      margin-top: .25em;
    }
    span{
      font-size: 1em;
      line-height: 144%;
    }
  }
}

.footer-bottom{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 3.75em;
  padding-top: 1.875em;
  border-top: 1px solid rgba($p,.2);
  &__conf{
    text-decoration: underline;
  }
  &__by{
    width: 7.5em;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.875em 0 1.25em;
    &__logo{
      width: 11em;
      margin-right: .375em;
    }
    .socials{
      margin: 0 0 0 .375em;
      &__item{
        width: 2.1875em;
        height: 2.1875em;
      }
      &__hint{
        max-width: 7.5em;
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
    &__text{
      @media screen and (min-width: 577px){
        display: grid;
        grid-template-columns: 8em 8.75em;
        justify-content: space-between;
      }
    }
  }

  .footer-top{
    justify-content: space-between;
    flex-wrap: wrap;
    &__info{
      display: flex;
      align-items: center;
    }
    &__col1{
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 1.875em;
    }
  }

  .footer-nav{
    margin-left: 0;
  }

  .footer-bottom{
    margin-top: 1.875em;
    padding-top: 1.25em;
  }
}




@media screen and (max-width: 576px){
  .footer{
    padding: 2.5em 0 5em;
    &__logo{
      margin-right: 1em;
    }
    &__text{
      margin-top: 0;
    }
    .socials{
      margin: 1.25em 0 0;
      &__hint{
        max-width: 12.5em;
        ._view2{
          display: none;
        }
        ._view1{
          display: block;
        }
      }
    }
  }

  .footer-top{
    display: block;
    &__col1{
      display: block;
      margin-bottom: .625em;
    }
    &__info{
      width: calc(100% + .25em);
      align-items: flex-start;
    }
  }

  .footer-nav{
    &._active{
      .footer-nav__btn svg{
        transform: rotate(-180deg);
      }
    }
    &__show{
      padding: .75em 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid rgba(255, 255, 255, 0.11);
    }
    &__btn{
      @include center;
      svg{
        width: .5em;
      }
    }
    &__hidden{
      display: none;
      padding-bottom: .625em;
    }
    &__link{
      br{
        display: none;
      }
    }
  }

  .footer-bottom{
    margin-top: 1.25em;
    flex-direction: column;
    &__conf,
    &__c{
      margin-bottom: .75em;
    }
  }
}
